.headline {
  border: 2px dotted #444;
  margin-bottom: 5px;
  padding: 5px;
  position: relative;
  background: -webkit-gradient(linear, left top, left bottom, from(#eee), to(rgb(173, 190, 189)));
  background: -webkit-linear-gradient(top, #eee, rgb(173, 190, 189));
  background: -o-linear-gradient(top, #eee, rgb(173, 190, 189));
  background: linear-gradient(to bottom, #eee, rgb(173, 190, 189));
}

.headline button {
  float: right;
  background: rgb(159, 233, 165);
}


